<template>
  <div>
    <div class="title">
      {{$route.query.k_id!=-1 ? "修改行业资讯":"增加行业资讯"}}
    </div>
    <div class="WOBox">
        <!-- 选择主题-下拉框、可以选多个 -->
        <div class="info">
          <el-select v-model="region" placeholder="请选择主题" class="Zinde">
            <el-option v-for="(item,index) in tagList" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </div> 
        <!-- 内容编辑区域 -->
      <div class="textBox">
        标题：<el-input v-model="input" class="inp" placeholder="请输入内容" maxlength="32"></el-input>
        <!-- 富文本插件start -->
        <div ref="editor" style="text-align:left;position: relative;z-index: 9;"></div>
        <!-- 富文本插件end -->
      </div>
      <el-button type="primary" @click="getContent">{{$route.query.k_id!=-1 ? "修改":"保存"}}</el-button>
    </div>
  </div>
</template>

<script>
  import E from 'wangeditor'
  import url from "../../common/api";
  import { mapState } from 'vuex'
  export default {
    data() {
      return {
        editorContent:'',//这是富文本的内容
        editor:null,//这是富文本继承的方法挂载变量
        input:'',//这是标题
        region:'',//这是选择的主题
      };
    },
    mounted() {
      this.editor = new E(this.$refs.editor)
      this.editor.customConfig.onchange = (html,a) => {
        this.editorContent = html
      }
      this.editor.customConfig.uploadImgServer =this.uploadimage   // 上传图片到服务器
      this.editor.customConfig.uploadFileName = 'image'
            this.editor.customConfig.uploadImgHooks = {  //监听
                customInsert: function (insertImg, result, editor) {
                    var url=result.ip + result.data[0]
                    insertImg(url)
                }
            }
      this.editor.customConfig.showLinkImg = false
      this.editor.create()
      if(this.$route.query.k_id!=-1){
        this.Fneditknowledge()
      }
    },
    methods: {
      getContent(){//点击保存按钮触发的事件
      
        if(this.$route.query.k_id==-1){
          // 保存
          if(this.input!='' && this.editorContent!='' && this.region!=''){
            console.log(this.region,'-----------这是看见')
            url.addknowledge({
              "user_id": this.buserInfo.user_id,
              "k_title": this.input,
              "k_type": "行业资讯",
              "k_content": this.editorContent,
              "k_tag":this.region
            }).then(res=>{
              console.log('----------添加行业资讯')
              if(res.data.code){
                this.$message.success(res.data.message[0]);
                this.$router.push('/bkIndex/bknews')
              }
            })          
          }else{
            this.$message.error('标题、主题以及问题内容不能为空');
          }
        }else{
          if(this.input!='' && this.editorContent!='' && this.region!=''){
            // 修改请求
            url.Peditknowledge({
              "user_id": this.buserInfo.user_id,
              "k_id": this.$route.query.k_id,
              "k_title": this.input,
              "k_content": this.editorContent,
              "k_tag":this.region
            }).then(res=>{
              console.log(res,'===========修改')
              if(res.data.code){
                this.$message.success(res.data.message[0]);
                this.$router.push('/bkIndex/bknews')
              }
            })  
          }else{
            this.$message.error('标题、主题以及问题内容不能为空');
          }
        }
        console.log(this.editorContent,'----------------------')
      },
      Fneditknowledge(){//查看单个的知识库详情
          url.editknowledge({
              k_id:this.$route.query.k_id
          })
          .then(res=>{
              console.log(res,'---------查看详情')
              if(res.data.code[0]='200'){
                  let xx=res.data.data[0].fields
                  xx.k_id=res.data.data[0].pk
                  this.det=xx
                  this.editor.txt.html(xx.k_content)
                  this.input=xx.k_title
                  this.region=xx.k_tag
              }
          })
      },
    },
    computed:{
      ...mapState(['buserInfo','uploadimage','tagList'])
    }
  };
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.WOBox
    padding 30px
    .info
      margin-bottom 20px
    .textBox
        width 950px
        height 667px
        margin-bottom 20px
        .inp
          font-size 24px
          margin-bottom 15px
          >>> .el-input__inner
            border none
.textBox >>> .w-e-text-container
  min-height 580px !important

</style>





